class CommentsList extends React.Component {
    render() {

        const {comments} = this.props;

        console.log('comments', comments);

        return(
            comments.map( (comment, index ) => {
                return <div>
                <div>{comment.user}</div>
                <div>{comment.content}</div>
                </div>
            })
        );
    }
}

export default CommentsList;